
nav {
    /* background:rgb(221, 232, 185); */
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    line-height: 1rem;
    overflow: hidden;
    padding-top: 10px;
    background-color: rgb(255, 242, 253);
    /* border-bottom: 2px solid  rgb(69, 50, 243); */
}

.active {
    font-size: large;
    font-weight: 700;
    /* text-shadow: 5px 5px 5px rgb(232, 210, 174); */
}

ul{
    list-style: none;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    padding: 0;
    margin: 0;
}

nav a {
    transition: all ease-in-out 0.1s;
    color: black;
   
}

nav a:hover {
    /* background-color: blueviolet; */
    transform: scale(1.1);
    text-decoration: underline;
    text-underline-offset: 5px;
    text-decoration-color: rgb(255, 0, 234);
}

li {
    flex: 1 0 10%;
    text-align: center;
    font-family: 'Nunito Sans', sans-serif;
    letter-spacing: 2px;
    font-weight: 600;
    color: rgb(0, 0, 19);
}

.logo, nav a {
    display: block;
    text-decoration: none;
    letter-spacing: .1rem;
    padding: 0.8rem;
}

.logo {
    margin: 0 auto;
    color: rgb(0, 0, 0);
    font-family: 'Staatliches', cursive;
    font-size: 55px;
    font-weight: 200;
    /* text-decoration: underline rgb(203, 133, 137) 2px; */
    text-underline-offset: 7px;
}

@media (min-width:600px) {
    nav {
        line-height: 1.5rem;
    }

    nav ul {
        width: 100%;
    }

    nav li {
        flex: 1 0 10%
    }

    .logo {
        
        font-size: 47px;
    }
}

@media (min-width: 900px){
    nav {
        justify-content: space-between;
    }

    nav ul {
        width: auto;
    }

    .logo {
        margin: 0 0 0 0.3rem;
        font-size: 47px;
    }
}